body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.p-card-staff-blog__body {
  background-color: #F7F7F7;
  overflow: hidden;
  position: relative;
  transition-duration: 0.4s;
  z-index: 2;
  border-radius: 0 0 10px 10px;
  padding: 1.1875rem 1.25rem 1rem 1.25rem;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
}

.p-card-staff-blog__body::after {
  background: #818A7D;
  border-radius: 47%;
  content: "";
  display: block;
  margin: auto;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  width: 100%;
  padding-top: 100%;
  height: 0;
  z-index: -1;
  transform: translateY(-50%) scale(0.1);
  transition: opacity 0.5s, transform 0s;
  transition-delay: 0s, 0.4s;
}

.p-staff-blog-card__block:hover .c-card-blog__title {
  color: #F7F7F7;
}

.p-staff-blog-card__block:hover .c-card-blog__text {
  color: #F7F7F7;
}

.p-staff-blog-card__block:hover .p-card-staff-blog__body::after {
  opacity: 1;
  transform: translateY(-50%) scale(1.1);
  transition-delay: 0s;
  transition: opacity 0.8s, transform 0.6s ease-in-out;
}